import React, { Component } from 'react';
import classNames from 'classnames';
import { MediaStatusTypes } from '../../Constants';
import Media from '../../components/CommonSet/Media';
import SummaryStyles from './Summary.cssmodule.less';

class CurriculumSummary extends Component {
    state = {
        index: 0
    }

    handleButtonClick(event, action) {
        entrylms.emit(action, { category: 'curriculum' }, null);
    }

    setIndex(n) {
        const max = this.props.data.contents.length - 1;
        const index = Math.max(0, n);
        return this.setState({ index: Math.min(index, max) });
    }


    createMediaView(media = {}) {
        const { category } = media;
        if (category && category !== MediaStatusTypes.NONE) {
            return <Media className={classNames(SummaryStyles[category], SummaryStyles.media)} media={media}
                          option={{ width: 504, height: 260 }}/>;
        }
        return null;
    }

    render() {
        const { contents = {} } = this.props.data;
        const { index } = this.state;
        const lastIndex = contents.length - 1;
        const content = contents[index];
        const { summary, media } = content;
        return (
            <div className={SummaryStyles.curriculum}>
                {/*   -  /   . */}
                <div className={SummaryStyles.intro_top}>
                    <div className={SummaryStyles.intro_inner}>
                        {/*   exclamation  /   question  */}
                        <div className={classNames(SummaryStyles.cell, SummaryStyles.exclamation)}>
                            <div className={SummaryStyles.thmb_group}>
                                <span className={SummaryStyles.thmb}>&nbsp;</span>
                                <strong className={SummaryStyles.title}></strong>
                            </div>
                               .
                        </div>
                    </div>
                </div>
                <div className={SummaryStyles.body}>
                    <div className={SummaryStyles.summary_box}>
                        <strong className={SummaryStyles.sjt}>
                            {summary}
                            <span className={SummaryStyles.step}>{index + 1}/{lastIndex + 1}</span>
                        </strong>
                        <div className={SummaryStyles.thmb_box}>
                            {this.createMediaView(media)}
                        </div>
                        <span
                            className={classNames(SummaryStyles.scroll_btn, SummaryStyles.prev, { [SummaryStyles.disabled]: index === 0 })}
                            onClick={() => this.setIndex(index - 1)}
                        >
                             
                         </span>
                        <span
                            className={classNames(SummaryStyles.scroll_btn, SummaryStyles.next, { [SummaryStyles.disabled]: index === lastIndex })}
                            onClick={() => this.setIndex(index + 1)}
                        >
                            
                        </span>
                    </div>
                </div>
            </div>
        );
    }
}

export default CurriculumSummary;



