'use strict';

import { Component } from 'react';
import Style from './Dropdown.cssmodule.less';
import React from 'react';

/**
 *
 * @param {String} placeholder    
 * @param {Array<String>} list   
 * @param {Object} option width: 276px .
 */
export default class Dropdown extends Component {
    constructor(props) {
        super(props);
        this.Style = Style;
        this.state = {
            isListVisible: false,
            headerContent: props.placeholder || '',
        };

        this.toggleDropdownList = this.toggleDropdownList.bind(this);
        this.makeItemList = this.makeItemList.bind(this);
    }

    toggleDropdownList() {
        this.setState({ isListVisible: !this.state.isListVisible });
    }

    /**
     *   re-render  
     *  . bind      arrow function  .
     *
     * @see makeItemList
     * @param item
     */
    handleItemClicked(item) {
        const { onItemChange } = this.props;
        if (onItemChange) {
            onItemChange(item);
        }

        this.setState({
            headerContent: item,
            isListVisible: false,
        });
    }

    makeItemList() {
        const style = this.Style;

        return (
            <div>
                {this.props.list.map((item, index) => {
                    return (
                        <div
                            key={index}
                            className={style.item}
                            onClick={() => {
                                this.handleItemClicked(item);
                            }}
                        >
                            {item}
                        </div>
                    );
                })}
            </div>
        );
    }

    render() {
        const style = this.Style;
        const { isListVisible, headerContent } = this.state;

        return (
            <div
                className={style.dropdown}
            >
                <div className={`${style.header} ${isListVisible && style.active}`} onClick={this.toggleDropdownList}>
                    <div
                        className={style.headerTitle}
                    >
                        {headerContent}
                    </div>
                    <div
                        className={style.arrowButton}
                    />
                </div>

                {isListVisible && (
                    <div className={style.list}>
                        {this.makeItemList()}
                    </div>
                )}
            </div>
        );
    }
};



