import { Component } from 'react';
import classNames from 'classnames';
import React from 'react';
import PrepareStyles from './Prepare.cssmodule.less';
import _cloneDeep from 'lodash/cloneDeep';

class Solutions extends Component {
    constructor(props) {
        super(props);
        const contents = this.props.contents || [];
        this.state = {
            index: 0,
            contents: contents.filter(content => {
                return content !== null;
            }).map(data => {
                const content = _cloneDeep(data);
                const solution = content.solution || [];
                content.solution = solution.map(sol => ({
                    content: sol,
                    isOpen: false,
                }));
                return content;
            }),
        };
        this.toggleClass = this.toggleClass.bind(this);
    }

    toggleClass(e, index) {
        e.preventDefault();
        const contentIndex = this.state.index;
        const contents = [...this.state.contents];
        contents[contentIndex].solution[index].isOpen = !contents[contentIndex].solution[index].isOpen;
        this.setState({ contents });
    }

    render() {
        const solutions = this.state.contents[this.state.index].solution;
        return (
            /*  step3 = prepare_step3   */
            <div>
                <div className={PrepareStyles.intro_top}>
                    <div className={PrepareStyles.intro_inner}>
                        <div className={`${PrepareStyles.cell} ${PrepareStyles.prepare_ready}`}>
                            <div className={PrepareStyles.thmb_group}>
                                <span className={PrepareStyles.thmb}>&nbsp;</span>
                                <strong className={PrepareStyles.title}></strong>
                            </div>
                            <ul className={PrepareStyles.prepare_step_list}>
                                <li>
                                    <em className={PrepareStyles.item_no}>1</em>
                                      
                                </li>
                                <li>
                                    <em className={PrepareStyles.item_no}>2</em>
                                    
                                </li>
                                <li className={PrepareStyles.active}>
                                    <em className={PrepareStyles.item_no}>3</em>
                                    
                                </li>
                            </ul>
                                   .
                        </div>
                    </div>
                </div>
                <div className={PrepareStyles.body}>
                    <div className={PrepareStyles.solution_box}>
                        <ul className={PrepareStyles.thmb_list}>
                            {/* li  on   */}
                            {this.state.contents.map((content, index) => {
                                if (!content.media || content.media.category !== 'IMAGE') {
                                    return;
                                }
                                return <li key={index}
                                           className={classNames(PrepareStyles.list, { [PrepareStyles.on]: this.state.index === index })}
                                           onClick={(e) => {
                                               this.setState({ index });
                                           }}>
                                    <img src={content.media.content}/>
                                </li>;
                            })}
                        </ul>
                        <div className={PrepareStyles.solution_cont}>
                            <div className={PrepareStyles.scroll_box}>
                                <strong className={PrepareStyles.solution_list_sjt}> </strong>
                                <ul className={PrepareStyles.solution_list}>
                                    {/* list   on   */}
                                    {solutions.map((solution, index) => {
                                        if(!solution.content) {
                                            return ;
                                        }
                                        return <li key={index}
                                                   className={classNames(PrepareStyles.list, { [PrepareStyles.on]: solution.isOpen })}
                                                   onClick={(e) => this.toggleClass(e, index)}>
                                            <div className={PrepareStyles.card}>
                                                <span className={PrepareStyles.ico}>{index}  </span>
                                            </div>
                                            <em className={PrepareStyles.text_box}>
                                                    <span className={PrepareStyles.inner}>
                                                        {solution.content}
                                                    </span>
                                            </em>
                                        </li>;
                                    })}
                                    <li className={PrepareStyles.list}>
                                        <em className={PrepareStyles.text_box}>
                                                    <span className={PrepareStyles.inner}>
                                                         
                                                    </span>
                                        </em>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Solutions;



